import React from 'react';

import './Styles/App.css';
import './Styles/Common.css'
import { Header } from './Component/Header';
import { AboutMe } from './Component/AboutMe';
import { Path } from './Component/Path';
import { Competence } from './Component/Competence';

export class App extends React.Component {

  constructor(props) {
    super(props);
    this.ref = React.createRef();
    this.header_ref = React.createRef();
    this.dataDate = null;
    this.ref = {};
    this.ref["aboutme"] = React.createRef();
    this.ref["path"] = React.createRef();
    this.ref["competence"] = React.createRef();
    this.ref["love"] = React.createRef();
    this.setLang(props.lang);
    this.loadAllLang();
  }

  event(type, event) {
    try {
      if(type === "scroll") {
        this.header_ref.current.close();
      } else if(type === "mousedown") {
        if(!this.header_ref.current.mainRef.current.contains(event.target))
          this.header_ref.current.close();
      }
    }catch{}
  }

  async loadAllLang() {
    this.allLang = (await require(`./l10n/.config.json`)).lang;
    this.forceUpdate();
  }

  setLang(lang) {
    this.lang = lang;
    this.loadTranslation();
  }

  changeLang(lang) {
    this.setLang(lang);
    this.componentDidMount();
  }

  async loadTranslation() {
    this.file = await require(`./l10n/${this.lang}.json`);
  }

  getValueTranslate(chemin, file) {
    if(chemin.length === 1) {
      return file[chemin[0]] === undefined ? "" : file[chemin[0]];
    } else {
      return this.getValueTranslate(chemin.slice(1), file[chemin[0]]);
    }
  }

  async componentDidMount() {
    await this.file;
    this.ref.current.querySelectorAll('[data-translate]').forEach(e => {
      let translateList = e.dataset.translate.split('_');
      let translateText = this.getValueTranslate(translateList, this.file);
      if(translateText.startsWith('ext-image:'))
        e.src = translateText.slice(10);
      else
        e.innerHTML = translateText;
    })
    this.dataDate = Object.keys(this.file.path.data);
    this.dataDateNb = {};
    for(let d of this.dataDate) {
      this.dataDateNb[d] = this.file.path.data[d].length;
    }
    this.forceUpdate();
  }

  scrollTo(to) {
    let pos = this.ref[to].current.mainRef.current.offsetTop - 205;
    window.scrollTo({
      left: 0, 
      top: pos,
      behavior: 'smooth'
    });
  }

  render() {
    let page = (
      <div className="App" ref={this.ref}>
        <Header onLangChange={(l) => this.changeLang(l)} lang={this.lang} allLang={this.allLang} ref={this.header_ref} onHeaderClick={(to) => this.scrollTo(to)}/>
        <AboutMe ref={this.ref.aboutme}/>
        <Path ref={this.ref.path} dataDate={this.dataDate} dataDateNb={this.dataDateNb} needUpdate={() => this.componentDidMount()}/>
        <Competence ref={this.ref.competence}/>
      </div>
    );
    return page;
  }
}
