import React from 'react';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import MenuIcon from '@material-ui/icons/Menu';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';

import '../Styles/Header.css'

export class Header extends React.Component {

    const_max_height = 171;
    const_min_height = 63;
    const_foot_height = 20;
    const_tick = 0;

    constructor(props) {
        super(props);
        this.view = false;
        this.height = 63;
        this.tel = React.createRef();
        this.mainRef = React.createRef();
    }

    onChangeLang(lang) {
        this.props.onLangChange(lang);
    }

    open() {
        this.height += this.const_foot_height;
        if(this.height >= this.const_max_height) {
            this.height = this.const_max_height;
        } else {
            if(this.height > (this.const_max_height / 2) && !this.view)
                this.view = true;
            setTimeout(() => this.open(), 0);
        }
        this.forceUpdate();
    }

    close() {
        this.height -= this.const_foot_height;
        if(this.height <= this.const_min_height) {
            this.height = this.const_min_height;
        } else {
            if(this.height < (this.const_max_height / 2) && this.view)
                this.view = false;
            setTimeout(() => this.close(), 0);
        }
        this.forceUpdate();
    }

    open_close() {
        if(this.view)
            this.close();
        else   
            this.open();
    }

    render() {
        let item = [];
        if(this.props.allLang !== undefined) {
            for(let l of this.props.allLang) {
                item.push(<MenuItem value={l} key={l}>{l}</MenuItem>);
            }
        }
        let display = (this.view ? "": "none");
        return (
            <span id="header" ref={this.mainRef}>
                <span id="tel" style={{height: this.height + 'px'}} ref={this.tel}>
                    <IconButton onClick={() => this.open_close()}>
                        <MenuIcon fontSize="large" />
                    </IconButton>
                    <ul id="tel_navbar" style={{display: display}} onClick={() => this.close()}>
                        <Button onClick={() => this.props.onHeaderClick('aboutme')}>
                            <li><span data-translate="header_aboutme"></span></li>
                        </Button>
                        <Button onClick={() => this.props.onHeaderClick('path')}>
                            <li><span data-translate="header_path"></span></li>
                        </Button>
                        <Button onClick={() => this.props.onHeaderClick('competence')}>
                            <li><span data-translate="header_competence"></span></li>
                        </Button>
                    </ul>
                </span>
                <ul id="navbar">
                    <Button onClick={() => this.props.onHeaderClick('aboutme')}>
                        <li><span data-translate="header_aboutme"></span></li>
                    </Button>
                    <Button onClick={() => this.props.onHeaderClick('path')}>
                        <li><span data-translate="header_path"></span></li>
                    </Button>
                    <Button onClick={() => this.props.onHeaderClick('competence')}>
                        <li><span data-translate="header_competence"></span></li>
                    </Button>
                </ul>
                <span id="lang">
                    <Select value={this.props.lang} onChange={l => this.onChangeLang(l.target.value)}>
                        {item}
                    </Select>
                </span>
            </span>
        )
    }
}
