import React from "react";
import { Tooltip, Typography } from "@material-ui/core";

import "../Styles/Competence.css";

import html from "../Images/logo/html.png";
import css from "../Images/logo/css.png";
import js from "../Images/logo/js.png";
import bootstrap from "../Images/logo/bootstrap.png";
import react from "../Images/logo/react.png";
import java from "../Images/logo/java.png";
import php from "../Images/logo/php.png";
import mysql from "../Images/logo/sql.png";
import python from "../Images/logo/python.png";
import angular from "../Images/logo/angular.png";
import git from "../Images/logo/git.png";
import jira from "../Images/logo/jira.png";
import trello from "../Images/logo/trello.png";
import linux from "../Images/logo/linux.png";
import cordova from "../Images/logo/cordova.png";
import office from "../Images/logo/office.png";
import jquery from "../Images/logo/jquery.png";
import ts from "../Images/logo/ts.png";
import cpp from "../Images/logo/cpp.png";
import c from "../Images/logo/c.png";
import ionic from "../Images/logo/ionic.png";
import expo from "../Images/logo/expo.png";
import intellij from "../Images/logo/intellij.png";
import vscode from "../Images/logo/vscode.png";
import vuejs from "../Images/logo/vuejs.png"


export class Competence extends React.Component {

    constructor(props) {
        super(props);
        this.mainRef = React.createRef();
        this.firstImg = React.createRef();
    }

    componentDidMount() {
        let h = this.firstImg.current.offsetWidth - 15;
        for(let icon of document.getElementsByClassName("cIcon"))
            icon.style.maxHeight = h + 'px';
        this.forceUpdate();
    }

    render() {
        return (
            <div ref={this.mainRef} className="competence">
                <div className="cContainer shadow">
                    <Typography className="cTitle" data-translate="competence_langage"></Typography>
                    <div className="cImage">
                        <Tooltip title="HTML" aria-label="HTML">
                            <img alt="html" src={html} className="cIcon" ref={this.firstImg}/>
                        </Tooltip>
                        <Tooltip title="CSS" aria-label="CSS">
                            <img alt="css" src={css} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="JavaScript" aria-label="JavaScript">
                            <img alt="js" src={js} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Java" aria-label="Java">
                            <img alt="java" src={java} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="PHP" aria-label="PHP">
                            <img alt="php" src={php} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="MySQL" aria-label="MySQL">
                            <img alt="mysql" src={mysql} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Python" aria-label="Python">
                            <img alt="python" src={python} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="TypeScript" aria-label="TypeScript">
                            <img alt="ts" src={ts} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="C++" aria-label="C++">
                            <img alt="cpp" src={cpp} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="C" aria-label="C">
                            <img alt="c" src={c} className="cIcon"/>
                        </Tooltip>
                    </div>
                </div>
                <div className="cContainer shadow">
                    <Typography className="cTitle" data-translate="competence_biblio"></Typography>
                    <div className="cImage">
                        <Tooltip title="Bootstrap" aria-label="Bootstrap">
                            <img alt="bootstrap" src={bootstrap} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="React / React native" aria-label="React">
                            <img alt="react" src={react} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Angular" aria-label="Angular">
                            <img alt="angular" src={angular} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Cordova" aria-label="Cordova">
                            <img alt="cordova" src={cordova} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="jQuery" aria-label="jQuery">
                            <img alt="jquery" src={jquery} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Ionic" aria-label="Ionic">
                            <img alt="ionic" src={ionic} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Vue.js" aria-label="Vue.js">
                            <img alt="vuejs" src={vuejs} className="cIcon"/>
                        </Tooltip>
                    </div>
                </div>
                <div className="cContainer shadow">
                    <Typography className="cTitle" data-translate="competence_appli"></Typography>
                    <div className="cImage">
                        <Tooltip title="Jira" aria-label="Jira">
                            <img alt="jira" src={jira} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Trello" aria-label="Trello">
                            <img alt="trello" src={trello} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Office" aria-label="Office">
                            <img alt="office" src={office} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Expo" aria-label="Expo">
                            <img alt="expo" src={expo} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="IntelliJ" aria-label="IntelliJ">
                            <img alt="intellij" src={intellij} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="VS Code" aria-label="VS Code">
                            <img alt="vscode" src={vscode} className="cIcon"/>
                        </Tooltip>
                    </div>
                </div>
                <div className="cContainer shadow">
                    <Typography className="cTitle" data-translate="competence_other"></Typography>
                    <div className="cImage">
                        <Tooltip title="Git / GitHub / GitLab" aria-label="Git / GitHub / GitLab">
                            <img alt="git" src={git} className="cIcon"/>
                        </Tooltip>
                        <Tooltip title="Linux" aria-label="Linux">
                            <img alt="linux" src={linux} className="cIcon"/>
                        </Tooltip>
                    </div>
                </div>
            </div>
        )
    }
}