import React from 'react'
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';

import IconButton from '@material-ui/core/IconButton';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import GitHubIcon from '@material-ui/icons/GitHub';

import profil from '../Images/profil.jpg';

import '../Styles/AboutMe.css'

export class AboutMe extends React.Component {

    constructor(props) {
        super(props);
        this.mainRef = React.createRef();
    }

    socialMedia(what) {
        switch(what) {
            case "linkedin":
                return "https://www.linkedin.com/in/samuel-casas-dray-939ab2170";
            case "phone":
                return "tel:+33760428727";
            case "mail":
                return "mailto:samuelcasasdray@gmail.com";
            case "github":
                return "http://github.com/samuel-casasdray";
            default:
                return;
        }
    }

    render() {
        return (
            <Card className="card shadow aboutme" ref={this.mainRef}>
                <CardMedia 
                    className="card-media"
                    image={profil}
                ></CardMedia>
                <CardContent>
                    <Typography className="title" data-translate="aboutme_title"></Typography>
                    <Typography className="text" data-translate="aboutme_text"></Typography>
                    <div className="separator"></div>
                    <div className="icons">
                        <Tooltip title="LinkedIn" aria-label="LinkedIn">
                            <IconButton href={this.socialMedia("linkedin")} target="_blank">
                                <LinkedInIcon style={{color: "#1976d2"}} fontSize="large" />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Phone" aria-label="Phone">
                            <IconButton href={this.socialMedia("phone")} target="_blank">
                                <ContactPhoneIcon style={{color: "#000"}} fontSize="large" />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Mail" aria-label="Mail">
                            <IconButton href={this.socialMedia("mail")} target="_blank">
                                <MailOutlineIcon style={{color: "#F55"}} fontSize="large" />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Github" aria-label="Github">
                            <IconButton href={this.socialMedia("github")} target="_blank">
                                <GitHubIcon style={{color: "#222"}} fontSize="large" />
                            </IconButton>
                        </Tooltip>
                    </div>
                </CardContent>
            </Card>
        )
    }
}