import React from 'react'
import { Typography, Button } from '@material-ui/core';

import IconButton from '@material-ui/core/IconButton';
import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';

import "../Styles/Path.css"

export class Path extends React.Component {

    DateRef = {};
    ButtonDateRef = {};
    DateNb = {};
    cssLine = {};
    color = ["rgba(211,136,168,1)", "rgba(191,150,182,1)", "rgba(169,165,197,1)", "rgba(148,179,211,1)", "rgba(126,195,226,1)", "rgba(105,209,240,1)"];

    constructor(props) {
        super(props);
        this.mainRef = React.createRef();
        this.lineRef = React.createRef();
        this.mobilePreviousRef = React.createRef();
        this.mobileAfterRef = React.createRef();
        this.mobilePreviousLiRef = React.createRef();
        this.mobileAfterLiRef = React.createRef();
    }

    componentDidUpdate() {
        if(this.props.dataDate !== null && Object.keys(this.DateRef).length === 0) {
            for(let d of this.props.dataDate) {
                this.DateRef[d] = React.createRef();
                this.ButtonDateRef[d] = React.createRef();
            }
            this.DateNb = this.props.dataDateNb;
            this.active = Object.keys(this.DateRef)[0];
            this.forceUpdate();
            this.props.needUpdate();
        }
    }

    getPrevious(date, list) {
        let key = Object.keys(list);
        let i = key.indexOf(date);
        if(i === 0) return list[date].current;
        return list[key[i - 1]].current;
    }

    getAfter(date, list) {
        let key = Object.keys(list);
        let i = key.indexOf(date);
        if(i === key.length - 1) return list[date].current;
        return list[key[i + 1]].current;
    }

    changeDate(date) {
        this.DateRef[this.active].current.style.display = "none";
        this.DateRef[date].current.style.display = "block";
        this.ButtonDateRef[this.active].current.classList.remove("mobile_active");
        this.ButtonDateRef[date].current.classList.add("mobile_active");
        this.getPrevious(this.active, this.ButtonDateRef).classList.remove('mobileshow');
        this.getAfter(this.active, this.ButtonDateRef).classList.remove('mobileshow');
        this.ButtonDateRef[this.active].current.classList.remove('mobileshow');
    
        this.getPrevious(date, this.ButtonDateRef).classList.add('mobileshow');
        this.getAfter(date, this.ButtonDateRef).classList.add('mobileshow');
        this.ButtonDateRef[date].current.classList.add('mobileshow');

        this.active = date;
        let bg = "linear-gradient(90deg, rgba(254,107,139,1) 0%, rgba(244,114,146,1) 45px, rgba(233,121,153,1) 90px";
        let nb = Object.keys(this.DateRef).indexOf(date);
        let i = 1;
        for(i; i <= nb; i++)
            bg += ", " + this.color[i] + " " + (90 + (100 * i)) + "px";
        bg += ", rgba(212,212,212,1) "  + (91 + (100 * nb)) + "px";
        bg += ", rgba(212,212,212,1) 90%, rgba(212,212,212,0.5) 95%, rgba(212,212,212,0) 100%)";
        this.lineRef.current.style.background = bg;
        this.forceUpdate();
    }

    mobileChangeDate(what) {
        let key = Object.keys(this.ButtonDateRef);
        let i = key.indexOf(this.active);
        if(what === "previous" && i > 0) {
            this.mobileAfterRef.current.classList.remove("disable");
            this.mobileAfterLiRef.current.classList.remove("mobileshow");
            if(i === 1) {
                this.mobilePreviousRef.current.classList.add("disable");
                this.mobilePreviousLiRef.current.classList.add("mobileshow");
            }
            this.changeDate(key[i - 1]);
        } else if(what === "after" && i < key.length - 1) {
            this.mobilePreviousRef.current.classList.remove("disable");
            this.mobilePreviousLiRef.current.classList.remove("mobileshow");
            if(i === key.length - 2) {
                this.mobileAfterRef.current.classList.add("disable");
                this.mobileAfterLiRef.current.classList.add("mobileshow");
            }
            this.changeDate(key[i + 1]);
        }
    }

    render() {
        let date = [];
        let text = [];
        let i = 0;
        let key = Object.keys(this.DateRef);
        for(let d of key) {
            date.push(
                <li key={d} ref={this.ButtonDateRef[d]} 
                    className={(i < 2 ? "mobileshow " : " ") + (i === 0 ? "mobile_active": "")}>
                    <Button 
                        className="dateButton active"
                        onClick={() => this.changeDate(d)}
                        data-text={(i + 1) + "/" + key.length}
                        data-translate={"path_date_" + d}
                    ></Button>
                </li>
            )
            var div = [];
            for(let j = 0; j < this.DateNb[d]; j++) {
                div.push( 
                    <div className="timeline-item">
                        <Typography data-translate={"path_data_" + d + "_" + j + "_title"} className="title"></Typography>
                        <Typography data-translate={"path_data_" + d + "_" + j + "_subtitle"} className="subtitle"></Typography>
                        <Typography data-translate={"path_data_" + d + "_" + j + "_text"} className="text"></Typography>  
                    </div>
                )
            }
            text.push(<div key={d} ref={this.DateRef[d]} style={{display: (i === 0 ? "" : "none")}}>{div}</div>);
            i++;
        }
        return (
            <div ref={this.mainRef} className="path shadow">
                <div className="timeline-date">
                    <ul className="listdate">
                        <IconButton className="mobile_previous" onClick={() => this.mobileChangeDate("previous")} ref={this.mobilePreviousRef}>
                            <KeyboardArrowLeftIcon fontSize="medium" />
                        </IconButton>
                        <li className="mobile_li mobileshow" ref={this.mobilePreviousLiRef}></li>
                        {date}
                        <li className="mobile_li" ref={this.mobileAfterLiRef}></li>
                        <IconButton className="mobile_after" onClick={() => this.mobileChangeDate("after")} ref={this.mobileAfterRef}>
                            <KeyboardArrowRightIcon fontSize="medium" />
                        </IconButton>
                    </ul>
                    <span className="line" ref={this.lineRef}></span>
                </div>
                <div className="timeline-text">
                    {text}
                </div>
            </div>
        )
    }
}